import { Account } from "../actionTypes";


export interface AuthState {
  status: 'checking' | 'authenticated' | 'not-authenticated' | 'incomplete-record';
  token: string | null;
  errorMessage: string;
  errorMessageLogin: string;
  loading: boolean;
  user: User | null;
}
export interface User {
  Apellidos: string;
  Direccion: string;
  Idioma: string;
  Nickname: string;
  Nombres: string;
  Raza: string;
  Sexo: string;
  Telefono: string;
  ___class: string;
  ciudadResidencia: string;
  created: number;
  departamentoResidencia: string;
  email: string;
  estaturaMetros: number;
  fechaNacimiento: number;
  imagenPerfil: string;
  numeroIdentificacion: number;
  objectId: string;
  ownerId: string;
  paisResidencia: string;
  pesoKilogramos: number;
  tipoIdentificacion: string;
  tipoSangre: string;
  tipoUsuario: string;
  updated: number;
}


const initValue: AuthState = {
  status: 'checking',
  token: null,
  errorMessage: '',
  errorMessageLogin: '',
  user: null,
  loading: false


};


const accountReducer = (state = initValue, action: any): AuthState => {

  switch (action.type) {
    case Account.addErrorLogin:
      return {
        ...state,
        user: null,
        status: 'not-authenticated',
        token: null,
        errorMessageLogin: action.payload,
        loading: false
      }
    case Account.addErrorRegister:
      return {
        ...state,
        user: null,
        status: 'not-authenticated',
        token: null,
        errorMessage: action.payload,
        loading: false
      }

    case Account.removeError:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        // status: 'not-authenticated'
      };

    case Account.signUp:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        status: 'authenticated',
        // token: action.payload['user-token'],
        user: action['payload']

      }

    case Account.login:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        status: 'authenticated',

        // token: action.payload['user-token'],
        user: action['payload']

      }
    case Account.imcompletRegist:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        status: "incomplete-record",
        user: action['payload']


      }

    case Account.logout:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        status: "not-authenticated",
      }

    case Account.loading:
      return {
        ...state,
        loading: action.payload
      }

    case Account.notAuthenticated:
      return {
        ...state,
        errorMessage: '',
        errorMessageLogin: '',
        status: 'not-authenticated',
        token: null,
        user: null
      }

    default:
      return state;
  }
};

export default accountReducer;