//Aciones - Puntuales 
export const loadingAction = (estado: boolean) => {
    const payload = estado
    const cargando = createActions( 
        Account.loading,
        { payload }
    );
    store.dispatch(cargando);
}

export const removeErrorGenral = () => {
    const payload = {}
    const cargando = createActions(
        Account.removeError,
        { payload }
    );
    store.dispatch(cargando);
}





//Porcedimientos Registrsos 
//------ Paso 1 --Nombres 
export const pasoUnoRegister = async (primerNombre: string, segundoNombre: string, primerApellido: string, segundoApellido: string): Promise<boolean> => {
    loadingAction(true);
    let isValid = true;
    let errorValidacion = "Valida la informacion Ingresada en:\n"
    if (primerNombre.trim().length === 0) { errorValidacion = errorValidacion + "*Primer Nombre Vacio\n"; isValid = false; }
    //if (segundoNombre.trim().length === 0) { errorValidacion = errorValidacion + "*Segundo Nombre Vacio \n"; isValid = false; }
    if (primerApellido.trim().length === 0) { errorValidacion = errorValidacion + "*Primer Apellido Vacio \n"; isValid = false; }
    if (segundoApellido.trim().length === 0) { errorValidacion = errorValidacion + "*Segundo Apellido Vacio \n"; isValid = false; }
    //    console.log(errorValidacion);
    if (!isValid) {
        const payload = errorValidacion;
        const _loginUser = createActions(
            Account.addErrorRegister,
            { payload }
        );
        store.dispatch(_loginUser);
        return false
    }
    await localStorage.setItem('paso1-register', JSON.stringify({ primerNombre, segundoNombre, primerApellido, segundoApellido }));
    if (await localStorage.getItem('paso1-register') !== "") return true
    const payload = 'En el momento no podemos validar tu solicitud, reintenta más tarde'
    const _loginUser = createActions(
        Account.addErrorRegister,
        { payload } 
    );
    store.dispatch(_loginUser);
    return false
}

