export const estartApp = async () => {
    let user: any = await localStorage.getItem('user')
    user = JSON.parse(user);
    console.log('Impresion Storage======', user);

    if (user) {
        loginUser(user.email, user.password, true);
        return
    }
    const payload = ''
    const _loginUser = createActions(
        Account.addErrorLogin,
        { payload }
    );
    store.dispatch(_loginUser);
}

