import React from 'react';
import { Routes, Route } from "react-router-dom";
/* import './App.css'; */
import Login from './pages/login/Login';
import About from './pages/about';
import { estartApp } from './redux/actions/account';
import { useSelector } from 'react-redux';
import { AuthState } from './redux/reducers/accountReducer';
import Support from './pages/support/Support';




function App() {
  const status: string =  useSelector((store: { account: AuthState }) => store.account.status);
  console.log(status)
  estartApp()
  console.log(status)

  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="about" element={<About />} />
        <Route path="support" element={<Support/>} />
      </Routes>

    </div>


  );
}

export default App;
