import { Link } from "react-router-dom";
import Logotipo from "../../assets/log/doctors.svg";
import { useSelector } from 'react-redux';
import { AuthState } from '../../redux/reducers/accountReducer';
import { AuthLayout } from '../AuthLayout';
import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Button, Grid, IconButton, InputAdornment, OutlinedInput, TextField, Tooltip } from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import ContactSupportIcon from '@mui/icons-material/ContactSupport';

interface State {
  amount: string;
  password: string;
  weight: string;
  weightRange: string;
  showPassword: boolean;
}


const Login = () => {
    const status: string =  useSelector((store: { account: AuthState }) => store.account.status);
    console.log(status)

    const [typeID, setTypeId] = React.useState('');

    const handleChange = (event: SelectChangeEvent) => {
      setTypeId(event.target.value as string);
    };

    const [values, setValues] = React.useState<State>({
      amount: '',
      password: '',
      weight: '',
      weightRange: '',
      showPassword: false,
    });
  
    const handleChange2 =
      (prop: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setValues({ ...values, [prop]: event.target.value });
      };
  
    const handleClickShowPassword = () => {
      setValues({
        ...values,
        showPassword: !values.showPassword,
      });
    };
  
    const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
      event.preventDefault();
    };

    return (
      <AuthLayout title="">
        <Grid container justifyContent="center">
          
          <Grid item xs={6} sm={6} mb={6}>
              <img src={Logotipo}></img>
          </Grid>

          <Grid item xs={ 12 } sx={{ mt: 2 }}>
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Tipo de Identificación</InputLabel>
            <Select
              required
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={typeID}
              label="Tipo de Identificación"
              onChange={handleChange}
            >
                <MenuItem value={1}>Cedula de Ciudadanía.</MenuItem>
                <MenuItem value={2}>Cedula de extranjería</MenuItem>
                <MenuItem value={3}>Menor sin identificación</MenuItem>
                <MenuItem value={4}>Adulto sin identificación</MenuItem>
                <MenuItem value={5}>Pasaporte</MenuItem>
                <MenuItem value={6}>Permiso especial de permanencia</MenuItem>
                <MenuItem value={7}>Registro civil</MenuItem>
                <MenuItem value={8}>Tarjeta de identidad</MenuItem>
                <MenuItem value={9}>Otro</MenuItem>
            </Select>
          </FormControl>
          </Grid>


          <Grid item xs={12} sx={{ mt: 2 }}>
              <TextField
                required
                id="numDocument"
                label="Número de Documento"
                name="numDocument"
                autoComplete="numDocument"
                fullWidth
              />
          </Grid>

          <Tooltip title="Ingrese año de nacimiento ó últimos 4 dígitos de tu celular">
          <Grid item xs={ 12 } sx={{ mt: 2 }}>
          <FormControl variant="outlined" fullWidth>
          <InputLabel htmlFor="outlined-adornment-password">Codigo verificación</InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={values.showPassword ? 'text' : 'password'}
            value={values.password}
            onChange={handleChange2('password')}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {values.showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
            label="Codigo verificación"
            placeholder="Año de Nacimiento ó últimos 4 dígitos de tu celular"
            required
          />
          </FormControl>
          </Grid>
          </Tooltip>

          <Grid
              container
              justifyContent="center"
              spacing={2}
              sx={{ mb: 4, mt: 1 }}
            >
              <Grid item xs={12} sm={6}>
                <Button variant="contained" fullWidth>
                  Ingresar
                </Button>
              </Grid>
          </Grid>

          <Grid container direction='row' justifyContent='center'>
            <Link to="/Support" style={{alignContent:'center'}}>
              <ContactSupportIcon titleAccess='Soporte'></ContactSupportIcon>
            </Link>
            <Link to="/Support" style={{alignContent:'center'}}>
              Soporte técnico
            </Link>
          </Grid>

      </Grid>
      </AuthLayout>
    );
  }

  export default Login